@mixin title-large {
	font-size: 4rem;
	font-weight: bold;
	font-family: monospace;
	line-height: 1;
}
@mixin border-box {
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}
@mixin separador($color) {
	&::after {
		content:"";
		display: block;
		width: 100px;
		height: 4px;
		background: $color;
	}
	
}



$cor-primaria: rgb(27, 223, 86);
$font-primaria: monospace;
$gutter: 60px;

section {
	max-width: 600px;
	margin: 0 auto;
	@include border-box();
}
.modal {
	h1 {
		color: $cor-primaria;
		font-family: $font-primaria;
		margin-bottom: $gutter;
		@include title-large;
		@include separador(blue);
	}

	p {
		font-size: 1.2em;
		line-height: 1.4;
		max-width: 400px;
		font-family: $font-primaria;
	}

	a {
		display: block;
		max-width: 120px;
		padding: 10px;
		border-radius: 4px;
		text-align: center;
		background: $cor-primaria;
		color: white;
		text-decoration: none;
		text-transform: uppercase;
		font-family: $font-primaria;
	}
}

ul {
	text-align: center;
	li {
		display: inline-block;
		list-style: none;
		a {
			display:block;
			padding: 20px;
			text-decoration: none;
			background-color: blue;
			color: white;
			border-radius: 8px;
			&:hover {
				background-color: pink;;
			}
		}
	}
}